.miniCalendar {
  display: flex; /* –î–≤—É—Ö–∑–æ–Ω–Ω—ã–π layout: –∫–∞–ª–µ–Ω–¥–∞—Ä—å + –∫–∞—Ä—Ç–∏–Ω–∫–∞ */
  align-items: stretch; /* –≠–ª–µ–º–µ–Ω—Ç—ã —Ä–∞—Å—Ç—è–≥–∏–≤–∞—é—Ç—Å—è –Ω–∞ –≤—Å—é –≤—ã—Å–æ—Ç—É */
  width: 100%;
  max-width: 100%; /* –ù–µ –≤—ã—Ö–æ–¥–∏—Ç –∑–∞ –ø—Ä–µ–¥–µ–ª—ã —Ä–æ–¥–∏—Ç–µ–ª—è */
  background: transparent;
  border-radius: 0;
  box-shadow: none;
  flex-shrink: 0;
  /* overflow: hidden —É–±—Ä–∞–Ω - –æ–±—Ä–µ–∑–∞–µ—Ç –∫—Ä—É–∂–æ–∫ –≤–æ–∫—Ä—É–≥ –¥–∞—Ç—ã */
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞–ª–µ–Ω–¥–∞—Ä–Ω–æ–π —Å–µ—Ç–∫–∏ (—Å–ª–µ–≤–∞) */
.calendarContent {
  flex: 0 0 66.67%; /* 2/3 —à–∏—Ä–∏–Ω—ã */
  padding: 0.75rem 1rem; /* –£–º–µ–Ω—å—à–µ–Ω padding –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö */
  display: flex;
  flex-direction: column;
  flex-shrink: 0; /* –ù–µ —Å–∂–∏–º–∞–µ—Ç—Å—è */
  min-width: 0; /* –ü–æ–∑–≤–æ–ª—è–µ—Ç flex-—ç–ª–µ–º–µ–Ω—Ç—É —Å–∂–∏–º–∞—Ç—å—Å—è –º–µ–Ω—å—à–µ –∫–æ–Ω—Ç–µ–Ω—Ç–∞ */
  border-top-left-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ª–µ–≤–æ–≥–æ –≤–µ—Ä—Ö–Ω–µ–≥–æ —É–≥–ª–∞ */
  border-bottom-left-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ª–µ–≤–æ–≥–æ –Ω–∏–∂–Ω–µ–≥–æ —É–≥–ª–∞ */
  box-sizing: border-box; /* Padding –≤–∫–ª—é—á—ë–Ω –≤ —à–∏—Ä–∏–Ω—É */
}

.monthName {
  font-size: 13px; /* –£–º–µ–Ω—å—à–µ–Ω –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö (–±—ã–ª–æ: 14px) */
  font-weight: 700;
  color: #000000; /* –ß—ë—Ä–Ω—ã–π —Ü–≤–µ—Ç (–±—ã–ª–æ: #8B008B —Ñ–∏–æ–ª–µ—Ç–æ–≤—ã–π) */
  margin-bottom: 6px; /* –£–º–µ–Ω—å—à–µ–Ω –æ—Ç—Å—Ç—É–ø */
  text-align: left;
  white-space: nowrap; /* –ù–µ –ø–µ—Ä–µ–Ω–æ—Å–∏—Ç—Å—è –Ω–∞ –Ω–æ–≤—É—é —Å—Ç—Ä–æ–∫—É */
  overflow: hidden;
  text-overflow: ellipsis; /* –û–±—Ä–µ–∑–∞–µ—Ç—Å—è —Å ... */
}

.weekDays {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
  margin-bottom: 4px;
}

.weekDay {
  font-size: 8px; /* –£–º–µ–Ω—å—à–µ–Ω –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö (–±—ã–ª–æ: 9px) */
  font-weight: 500;
  color: #999;
  text-align: center;
  padding: 1px 0;
}

.daysGrid {
  display: grid;
  grid-template-columns: repeat(7, 1fr);
  gap: 2px;
}

.day {
  aspect-ratio: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 10px; /* –£–º–µ–Ω—å—à–µ–Ω –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö (–±—ã–ª–æ: 11px) */
  font-weight: 500;
  color: var(--nastia-dark);
  position: relative;
  border-radius: 3px;
  transition: background-color 0.2s;
  min-width: 22px; /* –£–º–µ–Ω—å—à–µ–Ω –¥–ª—è –º–æ–±–∏–ª—å–Ω—ã—Ö (–±—ã–ª–æ: 26px) */
}

.day.otherMonth {
  color: #ccc;
  opacity: 0.5;
}

.day.targetDay {
  color: var(--nastia-red);
  font-weight: 700;
  z-index: 1;
  position: relative; /* –û–±–µ—Å–ø–µ—á–∏–≤–∞–µ—Ç —Ä–∞–±–æ—Ç—É z-index */
}

.handDrawnCircle {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 140%;
  height: 140%;
  margin-left: -70%; /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –≤–º–µ—Å—Ç–æ transform */
  margin-top: -70%;  /* –¶–µ–Ω—Ç—Ä–∏—Ä—É–µ–º –≤–º–µ—Å—Ç–æ transform */
  pointer-events: none;
  z-index: 10;
  opacity: 0.8;
  /* –£–±—Ä–∞–ª–∏ –∞–Ω–∏–º–∞—Ü–∏—é –¥–ª—è Safari */
}

@-webkit-keyframes drawCircle {
  from {
    stroke-dasharray: 200;
    stroke-dashoffset: 200;
    opacity: 0;
  }
  to {
    stroke-dasharray: 200;
    stroke-dashoffset: 0;
    opacity: 0.8;
  }
}

@keyframes drawCircle {
  from {
    stroke-dasharray: 200;
    stroke-dashoffset: 200;
    opacity: 0;
  }
  to {
    stroke-dasharray: 200;
    stroke-dashoffset: 0;
    opacity: 0.8;
  }
}

/* –ù–µ–±–æ–ª—å—à–∞—è –Ω–µ—Ä–æ–≤–Ω–æ—Å—Ç—å –≤ SVG path –¥–ª—è hand-drawn —ç—Ñ—Ñ–µ–∫—Ç–∞ */
.handDrawnCircle path {
  filter: url(#roughness);
}

/* –ö–æ–Ω—Ç–µ–π–Ω–µ—Ä –¥–ª—è –∫–∞—Ä—Ç–∏–Ω–∫–∏ (—Å–ø—Ä–∞–≤–∞) */
.imageContainer {
  position: relative; /* –î–ª—è –∞–±—Å–æ–ª—é—Ç–Ω–æ–≥–æ –ø–æ–∑–∏—Ü–∏–æ–Ω–∏—Ä–æ–≤–∞–Ω–∏—è –∫–Ω–æ–ø–∫–∏ */
  flex: 0 0 33.33%; /* 1/3 —à–∏—Ä–∏–Ω—ã */
  padding: 0 !important; /* –ë–µ–∑ –ø–∞–¥–¥–∏–Ω–≥–∞ - –∫–∞—Ä—Ç–∏–Ω–∫–∞ –¥–æ –∫—Ä–∞—è */
  margin: 0 !important; /* –ë–µ–∑ –æ—Ç—Å—Ç—É–ø–æ–≤ */
  overflow: hidden; /* –°–∫—Ä—É–≥–ª—è–µ–º —É–≥–ª—ã */
  border-top-right-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ø—Ä–∞–≤–æ–≥–æ –≤–µ—Ä—Ö–Ω–µ–≥–æ —É–≥–ª–∞ */
  border-bottom-right-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ø—Ä–∞–≤–æ–≥–æ –Ω–∏–∂–Ω–µ–≥–æ —É–≥–ª–∞ */
  flex-shrink: 0; /* –ù–µ —Å–∂–∏–º–∞–µ—Ç—Å—è */
  box-sizing: border-box; /* Padding –≤–∫–ª—é—á—ë–Ω –≤ —à–∏—Ä–∏–Ω—É */
  align-self: stretch; /* –í–ê–ñ–ù–û: –†–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç—Å—è –Ω–∞ –≤—Å—é –≤—ã—Å–æ—Ç—É —Ä–æ–¥–∏—Ç–µ–ª—è */
  min-height: 100%; /* –ì–∞—Ä–∞–Ω—Ç–∏—Ä—É–µ–º –º–∏–Ω–∏–º–∞–ª—å–Ω—É—é –≤—ã—Å–æ—Ç—É */
  display: flex; /* –î–µ–ª–∞–µ–º flex –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–æ–º */
  align-items: stretch; /* –†–∞—Å—Ç—è–≥–∏–≤–∞–µ–º –∫–∞—Ä—Ç–∏–Ω–∫—É */
}

/* –ö–∞—Ä—Ç–∏–Ω–∫–∞ –≤–Ω—É—Ç—Ä–∏ –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
.image {
  width: 100% !important; /* –ó–∞–ø–æ–ª–Ω—è–µ—Ç –≤—Å—é —à–∏—Ä–∏–Ω—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
  height: 100% !important; /* –ó–∞–ø–æ–ª–Ω—è–µ—Ç –≤—Å—é –≤—ã—Å–æ—Ç—É –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–∞ */
  object-fit: cover !important; /* –ó–∞–ø–æ–ª–Ω—è–µ—Ç –≤–µ—Å—å –±–ª–æ–∫, –æ–±—Ä–µ–∑–∞—è –ª–∏—à–Ω–µ–µ —Å —Å–æ—Ö—Ä–∞–Ω–µ–Ω–∏–µ–º –ø—Ä–æ–ø–æ—Ä—Ü–∏–π */
  object-position: center center !important; /* –¶–µ–Ω—Ç—Ä–∏—Ä–æ–≤–∞–Ω–∏–µ –ø–æ –æ–±–µ–∏–º –æ—Å—è–º */
  display: block !important;
  flex: 1 1 auto; /* –†–∞—Å—Ç—è–≥–∏–≤–∞–µ—Ç—Å—è –≤–æ flex –∫–æ–Ω—Ç–µ–π–Ω–µ—Ä–µ */
  vertical-align: top; /* –£–±–∏—Ä–∞–µ—Ç –≤–æ–∑–º–æ–∂–Ω—ã–π inline –æ—Ç—Å—Ç—É–ø */
  margin: 0 !important; /* –£–±–∏—Ä–∞–µ—Ç –≤–æ–∑–º–æ–∂–Ω—ã–µ –æ—Ç—Å—Ç—É–ø—ã */
  padding: 0 !important; /* –£–±–∏—Ä–∞–µ—Ç –≤–æ–∑–º–æ–∂–Ω—ã–µ –ø–∞–¥–¥–∏–Ω–≥–∏ */
  border: 0 !important; /* –£–±–∏—Ä–∞–µ–º border –µ—Å–ª–∏ –µ—Å—Ç—å */
  border-top-right-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ø–µ—Ä–µ–Ω–æ—Å–∏–º –Ω–∞ —Å–∞–º—É –∫–∞—Ä—Ç–∏–Ω–∫—É */
  border-bottom-right-radius: 1rem; /* –°–∫—Ä—É–≥–ª–µ–Ω–∏–µ –ø–µ—Ä–µ–Ω–æ—Å–∏–º –Ω–∞ —Å–∞–º—É –∫–∞—Ä—Ç–∏–Ω–∫—É */
}

/* –ö–Ω–æ–ø–∫–∞ —É–¥–∞–ª–µ–Ω–∏—è –ø–æ–≤–µ—Ä—Ö –∫–∞—Ä—Ç–∏–Ω–∫–∏ */
.deleteButton {
  position: absolute;
  top: 8px;
  right: 8px;
  width: 32px;
  height: 32px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.95);
  backdrop-filter: blur(8px);
  border: none;
  display: flex;
  align-items: center;
  justify-content: center;
  cursor: pointer;
  transition: all 0.2s ease;
  z-index: 10;
  color: #666; /* –°–µ—Ä—ã–π —Ü–≤–µ—Ç –∏–∫–æ–Ω–∫–∏ (–±—ã–ª–æ: —Å–∏–Ω–∏–π currentColor) */
  padding: 0;
}

.deleteButton:hover {
  background: rgba(255, 255, 255, 1);
  transform: scale(1.05);
  color: #333;
}

.deleteButton:active {
  transform: scale(0.95);
}
